.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 20px;
  padding: 20px;
  color: white;
  position: absolute;
  width: 100%;
}

#App-title {
  font-size: 1.2em;
  margin: 0px;
  float: left;
  padding: 0px 15px 0px 15px;
  border-right-style: solid;
}

.App-intro {
  font-size: large;
  height: 800px;
  position: fixed;
  width: 100%;
  top: 60px;
  background-color: grey;
  margin: 0px;
}



